@media (min-width: 2560px) {
    .main-section {
        &.requirements {
            height: 692px;
            width: 100%;
            background-image: url(../../../assets/img/third-block-bg/third-block-bg-2560x692.jpg);
            background-size: cover;

            .requirements-header {
                text-align: center;
                padding-top: 130px;
                padding-left: 0px;
    
                margin-bottom: 29px;
            }
            .requirements-content {
                p {
                    margin-left: -15px;
                    width: 549px;
                }
            }
            .requirements-img {
                .manLaptopV2 {
                    display: none
                }
                .manLaptopV1 {
                    margin: auto;
                    margin-top: 20px;
                    padding-left: 30px;
                    width: 429px;
                    height: 361px;
                    display: block
                }
            }
        }
    }
}
