@media (max-width: 767px) {
    .main-section {
        &.requirements {
            height: inherit;
            width: 100%;
            background-image: url(../../../assets/img/third-block-bg/third-block-bg-320x1031.jpg);
            background-size: cover;

            .requirements-header {
                text-align: center;
                padding-top: 60px;
                padding-left: 0px;
    
                margin-bottom: 0px;
            }
            .requirements-content {
                flex-direction: column-reverse;

                p {
                    padding-left: 0px;
                    margin-top: 0px;
                    margin-bottom: 55px;
                    width: 100%;
                    text-align: center;
                }

                .requirements-img {
                    .manLaptopV2 {
                        display: none
                    }
                    .manLaptopV1 {
                        margin: auto;
                        margin-top: 30px;
                        padding-left: 15px;
                        width: 257px;
                        height: 216px;
                        display: block
                    }
                }
            }
        }
    }
}

